div.Card {
  width: 350px;
  border-radius: 8px;
  box-shadow: 0px 1px 2px rgba(28, 30, 34, 0.08), 0px 8px 16px rgba(28, 30, 34, 0.08);
}
div.Card .cars {
  width: 350px;
  height: 220px;
  border-top-left-radius: 8px;
  border-top-right-radius: 8px;
}
div.Card .button {
  position: relative;
  left: 310px;
  top: -210px;
  width: 30px;
}
div.Card div {
  margin-top: 0;
  height: 27%;
}
div.Card div :nth-child(3) {
  font-weight: bold;
}
div.Card div :nth-child(2) {
  color: grey;
}
div.Card div h3 {
  margin-top: 0;
  height: 30%;
}
div.Card div p {
  height: 30%;
}

